// Mobil design
.navbar-container
    @include width-height(100%, 7rem)
    @include set-position(fixed,0,0)
    z-index: 10

    // Blurred overlaying background
    .background-layer
        @include width-height(100vw, 100%)
        @include set-position(fixed,0,0)
        z-index: -1
        background-color: rgba(230,230,255,0.92)
        transition: all 0.8s ease
        visibility: hidden
        opacity: 0
    .expand
        visibility: visible
        opacity: 1

    // Top container (contain photo, name and Burger btn nor links )
    .top-container
        @include width-height(100vw, 6rem)
        @include flexbox(row, space-between, center,0)
        z-index: 10
        background-color: rgb(25,25,45) // Marine Blue

        div
            @include flexbox(row, space-between, center,1.5rem)
            margin-left: 1rem
            
            //Navbar profil photo
            .profil-photo // rounded by class
                @include width-height(4.4rem, 4.4rem)
                @include set-background-image('../assets/shared/profil.jpg', center, cover)

            // Navbar Brand
            .navbar-title
                @include flexbox(row, center, center)
                @include set-font(white,Source Code Pro, 2.4rem, 300,normal )
                letter-spacing: 0.2rem
        
        // Burger Btn
        .menu-btn
            @include width-height(4rem, 4rem)
            display: block
            margin-right: 2rem

    // Links container (depends on viewport)
    .links-container-wrapped
        @include width-height(100vw, auto)
        @include flexbox(column, center,flex-start)
        @include set-position(fixed, -24rem ,undefined,undefined,0)
        z-index: -1
        background-color: rgb(24,24,45) // Marine Blue
        visibility: hidden
        transition: all 0.4s ease
        box-shadow: 0px 8px 21px -2px rgba(24,29,66,0.88)

        // Each Link
        .link
            @include flexbox(row, flex-start, center, 1.5rem)
            @include width-height(100%, 6rem)
            @include set-font(white,Roboto, 1.5rem, 300,normal )
            padding-left: 2rem
            border-top: 1px solid rgba(250,250,250,0.2)

        // The double arrow wrap btn 
        .wrap-menu-btn
            @include width-height(100%, 6rem)
            text-align: center

    .expand
        visibility: visible
        top: 6rem

// Tablet Design
@include respond(tablet)
    .navbar-container
        @include width-height(100vw, 7rem)
        @include flexbox(row, space-between, center)
        background-color: rgb(25,25,45) // Marine Blue

        // Only the name and the photo
        .top-container
            @include width-height(40%, 7rem)
            .navbar-title
                font-size: 4rem
                padding: 0 2rem 0 2rem
            .menu-btn
                display: none
        .links-container-wrapped
            display: none
        .links-container-unwrapped
            width: auto
            color: white
            font-size: 2rem
            display: flex
            justify-content: space-between
            align-items: center
            gap: 2.5rem
            padding-right: 2rem
            .link
                color: white
                font-size: 2rem
                font-weight: 300
                padding: 1.5rem 1rem 1.5rem 1rem
                height: 7rem
                display: flex
                align-items: center
                position: relative

                .selector
                    position: absolute
                    width: 100%
                    height: 0.4rem
                    background-color: white
                    bottom: 0
                    left: 0
                    display: none

                .selected
                    display: block
@include respond(desktop)
    .navbar-container
        width: 100%
        left: 50%
        transform: translateX(-50%)
        .top-container
            div
                padding: 0
                margin-left: 5rem
                .navbar-title
                    font-size: 2.4rem
                    padding: 0 3rem 0 3rem
                .menu-btn
                    display: none
        .links-container-wrapped
            display: none
        .links-container-unwrapped
            padding: 0
            margin-right: 5rem


    
